@import '../vars';

#history-screen {
  button {
    cursor: pointer;
  }
  .head-panel {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $border-b-color;
    color: $primary-color;
    font-size: 14px;
    font-weight: 500;
    padding: 0 12px;
    height: 56px;
    background-color: #18181B;

    div {
      display: flex;
      align-items: center;
    }

    .title-sidepanel {
      color: $primary-color;
      font-size: 20px;
      font-weight: 700;
      width: 80px;
      margin: 0px;
    }

    img {
      width: 75px;
      height: 40px;
    }

    button {
      color: $primary-color;
      border: none;
      background-color: transparent;
      padding: 0px;

      #menu-icon {
        width: 40px;
        height: 40px;
      }
    }

    .right-btn-row {
      .custom-tooltip {
        position: absolute;
        margin-left: -100px;
        z-index: 99;

        .content-tooltip {
          padding: 6px 8px;
          border-radius: 4px;
          background-color: #303036;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          p {
            margin: 0px;
            color: #ffff;
          }
        }

        .after-tooltip {
          border-color: transparent transparent transparent #303036;
          width: 0px;
          height: 0px;
          border-style: solid;
          border-width: 4px 0px 4px 4px;
        }
      }
    }

    .go-to-his {
      width: 80px;
      display: flex;
      justify-content: flex-end;
    }
  }
  .body-panel {
    height: calc(100% - 71px);
    .his-chat-panel {
      padding: 16px 16px 0 16px;
      height: 98%;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #808080;
        border-radius: 99px;
      }
      .item-his-view {
        width: 100%;
        text-align: left;
        color: #c9c9cf;
        background-color: transparent;
        border: none;
        padding: 16px 0px;
        border-bottom: 1px solid $border-b-color;
        font-family: 'Roboto', sans-serif;
        p {
          margin: 0px;
          font-size: 14px;
          margin-top: 8px;
          overflow: hidden;
          text-overflow: ellipsis;
          word-break: break-all;
          white-space: nowrap;
        }
        .title-item-his-view {
          font-size: 16px;
          color: $primary-color;
          margin-top: 0px;
        }
      }
    }

    .no-data-view {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      img {
        width: 250px;
        height: 170px;
      }

      p {
        font-weight: 300;
        font-size: 32px;
        color: #afafb7;
        margin: 8px 0px 38px 0px;
      }

      button {
        color: #ffff;
        background: #fd2f4a;
        border: none;
        font-weight: 500;
        font-size: 14px;
        padding: 10px 12px;
        border-radius: 4px;
      }
    }
  }
}
